.circle {
    fill: none;
    stroke-width: 15;
}

.circle__first {
    stroke: #dfdcd5;
}

.circle__second {
    stroke: #1bb696;
    stroke-dasharray: .0010 282.6;
    transition: all 1s;
}

.circle:hover .circle__second--0 {
    stroke-dasharray: .001 282.6;
 }
 .circle:hover .circle__second--5 {
   stroke-dasharray: 14.13 282.6;
 }
 .circle:hover .circle__second--10 {
    stroke-dasharray: 28.26 282.6;
 }
 .circle:hover .circle__second--15 {
    stroke-dasharray: 42.39 282.6;
 }
